import React, { useState } from 'react'

import {
    Button,
    Grid,
    Typography
} from '@material-ui/core'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
  faTrashAlt
} from '@fortawesome/free-solid-svg-icons'

import { colors } from '../../styles/theme'

export const SelectedStat = (props) => {
    const {
        stat,
        removeStat
    } = props

    const [isHovering, setIsHovering] = useState(false)

    return (
        <Grid container style={styles.container}
            onMouseEnter={() => setIsHovering(true)}
            onMouseLeave={() => setIsHovering(false)}>
            <Button style={styles.removeStat}
                onClick={removeStat}>
                <div style={{
                        display: isHovering ? 'block' : 'none', 
                        position: 'absolute', width: '100%', height: '100%', zIndex: 999, backgroundColor: `${colors.theme.red}33`
                    }}>
                    <FontAwesomeIcon icon={faTrashAlt} color="white" size="lg" style={{marginTop: 15}} />
                </div>
                <Grid item xs={2} style={{
                        opacity: isHovering ? 0.2 : 1,
                        ...styles.statCost, 
                        backgroundColor: stat.color,
                        color: stat.cost === 4 ? colors.theme.white : colors.theme.black
                    }}>
                    <Typography>
                        {stat.points}
                    </Typography>
                </Grid>
                <Grid item xs style={{ opacity: isHovering ? 0.2 : 1 }}>
                    <Typography style={styles.statName}>
                        {stat.name}
                    </Typography>
                </Grid>
            </Button>
        </Grid>
    )
} 

export default SelectedStat

const styles = {
    container: {
        border: `1px solid ${colors.theme.goldPrimary}`,
        textAlign: 'center',
        alignItems: 'center',
        alignContent: 'center'
    },

    statName: {
        color: colors.theme.goldPrimary,
        paddingLeft: 10,
        alignItems: 'center',
        alignContent: 'center'
    },
    statCost: {
        textAlign: 'center',
        border: `1px solid ${colors.theme.goldPrimary}`,
        borderRadius: 7,
        width: 15,
        margin: 2,
        marginLeft: 10
    },

    removeStat: {
        width: '100%',
        margin: 0,
        padding: 0,
        minHeight: 50
    }
}